import {
    BottomNavigation,
    BottomNavigationAction,
    Box,
    Fade,
    Paper,
    Slide,
} from "@mui/material";
import {
    PeopleAltOutlined,
    RocketLaunchOutlined,
    TodayOutlined,
    ArrowForwardIos,
} from "@mui/icons-material";
import "./css/glass-button.css";
import { LinearProgress, linearProgressClasses, styled } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2/Grid2";
import { useLocation, useNavigate } from "react-router-dom";
import useSound from "use-sound";
import { useState } from "react";
import { grey, red } from "@mui/material/colors";
import Odometer from "react-odometerjs";

const Navbar = ({
    theme,
    maxEnergy,
    energy,
    transition,
    setTransition,
    karakterHealth,
    maxKarakterHealth,
    balance,
    settings,
    user,
}) => {
    const [navValue, setNavValue] = useState("/click-game", "navbar");
    const logo = config.root + "/assets/images/logo1.png";
    const click_sound = config.root + "/assets/sounds/click1.mp3";
    const [playClickSound] = useSound(click_sound);
    const location = useLocation();
    const navigate = useNavigate();
    const telegram = window.Telegram.WebApp;const config = require("../config.json");
    // const countUpRef = useRef(null);
    // const countUp = useCountUp({
    //     ref: countUpRef,
    //     start: 0,
    //     end: balance,
    //     delay: 200,
    //     duration: 1.3,
    //     plugin: new Odometer({ duration: 2.3, lastDigitDelay: 0 }),
    // });
    const EnergyLinearProgress = styled(LinearProgress)(({ theme }) => ({
        height: 8,
        borderRadius: 2.7,
        [`&.${linearProgressClasses.colorPrimary}`]: {
            backgroundColor: theme.palette.grey[800],
        },
        [`& .${linearProgressClasses.bar}`]: {
            borderRadius: 2.7,
            backgroundColor: "#ffb600",
        },
    }));
    const HealthLinearProgress = styled(LinearProgress)(({ theme }) => ({
        height: 19,
        borderRadius: 2.7,
        [`&.${linearProgressClasses.colorPrimary}`]: {
            backgroundColor: theme.palette.grey[800],
        },
        [`& .${linearProgressClasses.bar}`]: {
            borderRadius: 2.7,
            backgroundColor: red[600],
        },
    }));

    return (
        <Box display="flex" justifyContent="center" alignItems="center">
            {location.pathname === "/" ? (
                <Grid
                    container="true"
                    position={"fixed"}
                    display={"flex"}
                    top={0}
                    width={"100%"}
                    justifyContent={"center"}
                    alignItems={"center"}
                >
                    <Grid xs={12} width={"100%"}>
                        <Fade in={transition}>
                            <Grid
                                role="button"
                                onClick={() => {
                                    if (settings.menuSounds) playClickSound();
                                    navigate("/profile");
                                }}
                                sx={{
                                    color: "white",
                                    backgroundColor: "#252525",
                                    width: "100%",
                                    height: 60,
                                    borderRadius: "0 0 1rem 1rem",
                                    padding: 1,
                                    boxShadow: "0 1px 7px rgba(20,20,20,0.8)",
                                }}
                            >
                                <Grid
                                    container="true"
                                    displa="flex"
                                    alignItems="center"
                                    gap={1}
                                >
                                    <Grid xs={1.5}>
                                        <img
                                            style={{
                                                borderRadius: "50%",
                                                height: "2.75rem",
                                                border:
                                                    "1px solid " + grey[800],
                                            }}
                                            src={
                                                config.root + "/assets/images/characters/" +
                                                user.selfKarakter.id +
                                                ".jpg"
                                            }
                                            alt=""
                                        />
                                    </Grid>
                                    <Grid xs={5.5}>
                                        <div
                                            style={{
                                                fontSize: "18px",
                                                marginTop: "-6px",
                                            }}
                                        >
                                            {user.name}
                                        </div>
                                        <div
                                            style={{
                                                color: grey[400],
                                                fontFamily: "sans-serif",
                                                marginTop: "-6px",
                                            }}
                                        >
                                            {user.selfKarakter.name}
                                        </div>
                                    </Grid>
                                    <div
                                        style={{
                                            position: "fixed",
                                            right: 20,
                                            color: grey[400],
                                        }}
                                    >
                                        <ArrowForwardIos fontSize="20px" />
                                    </div>
                                </Grid>
                            </Grid>
                        </Fade>
                    </Grid>
                    <Grid
                        xs={12}
                        sx={{
                            minWidth: "240px",
                            maxWidth: "255px",
                            top: 0,
                            left: 0,
                            right: 0,
                            marginTop: "1rem",
                        }}
                    >
                        <Slide
                            direction="down"
                            in={transition}
                            mountOnEnter
                            unmountOnExit
                        >
                            <Paper
                                className={`btn ${theme}`}
                                sx={{
                                    // border: `1.5px solid ${theme}`,
                                    // boxShadow: `0 0 3px ${theme}`,
                                    display: "flex",
                                    justifyContent: "center",
                                    padding: "0.25vh",
                                    paddingRight: "1.5rem",
                                    borderRadius: "10px",
                                    minWidth: "240px",
                                    maxWidth: "355px",
                                    top: 0,
                                    left: 0,
                                    right: 0,
                                    WebkitTextStroke: ".7px black",
                                }}
                            >
                                <Grid
                                    container="true"
                                    width={"100%"}
                                    display={"flex"}
                                    alignItems={"center"}
                                    justifyContent="center"
                                    gap={1.5}
                                >
                                    <Grid
                                        xs={3}
                                        display="flex"
                                        justifyContent="center"
                                        alignItems="center"
                                        marginLeft={0.35}
                                    >
                                        <img
                                            src={logo}
                                            alt=""
                                            style={{
                                                height: "4rem",
                                            }}
                                        />
                                    </Grid>
                                    <Grid
                                        maxHeight={"80px"}
                                        display="flex"
                                        justifyContent="start"
                                        alignItems="center"
                                        marginLeft={0.35}
                                        xs={8}
                                        textAlign={"start"}
                                    >
                                        <h1
                                            style={{
                                                fontSize: "35px",
                                                whiteSpace: "nowrap",
                                            }}
                                        >
                                            {/* {balance.toLocaleString()}  */}
                                            <Odometer
                                                value={parseInt(balance)}
                                            />
                                        </h1>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Slide>
                    </Grid>
                </Grid>
            ) : (
                ""
            )}
            <Slide direction="up" in={transition} mountOnEnter unmountOnExit>
                <Paper
                    sx={{
                        position: "fixed",
                        bottom: 0,
                        left: 0,
                        right: 0,
                        background: "rgb(35, 35, 35)",
                        borderRadius: "14px 14px 0px 0px",
                        boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.6)",
                        backdropFilter: "blur(4.6px)",
                        WebkitBackdropFilter: "blur(4.6px)",
                        zIndex: 999998,
                    }}
                >
                    {location.pathname === "/click-game" ? (
                        <center>
                            <Grid
                                display={"flex"}
                                maxWidth={"724px"}
                                alignItems={"center"}
                                justifyContent={"center"}
                                container="true"
                                paddingLeft={"10px"}
                                paddingRight={"10px"}
                            >
                                <Grid xs={9} marginTop={0.8}>
                                    <HealthLinearProgress
                                        variant="determinate"
                                        value={
                                            (karakterHealth /
                                                maxKarakterHealth) *
                                            100
                                        }
                                        sx={{ marginBottom: 0.5 }}
                                    />
                                    <EnergyLinearProgress
                                        variant="determinate"
                                        value={(energy / maxEnergy) * 100}
                                    />
                                </Grid>
                                <Grid
                                    xs={3}
                                    textAlign={"center"}
                                    paddingTop={"5px"}
                                >
                                    <span style={{ fontSize: "26px" }}>
                                        <Odometer value={energy} />
                                    </span>
                                </Grid>
                            </Grid>
                        </center>
                    ) : (
                        ""
                    )}
                    <BottomNavigation
                        style={{
                            background: "none",
                            display: "flex",
                            columnGap: "10px",
                            padding: "10px",
                            paddingTop: "0px",
                            marginTop: "5px",
                        }}
                        value={navValue}
                        onChange={(event, newValue) => {
                            if (navValue === newValue) return;
                            setTransition(false);
                            if (settings.menuSounds) playClickSound();
                            setTimeout(() => {
                                setNavValue(newValue);
                                telegram.BackButton.show();
                                telegram.BackButton.onClick(() => {
                                    navigate(-1);
                                    telegram.BackButton.hide();
                                });
                                setTransition(true);
                                navigate(newValue);
                            }, 300);
                        }}
                    >
                        <BottomNavigationAction
                            className={"btn " + theme}
                            label="Friends"
                            value="/friends"
                            icon={<PeopleAltOutlined />}
                        />
                        {/* <BottomNavigationAction
                            className={"btn " + theme}
                            label="Clicker"
                            value="/click-game"
                            icon={<MonetizationOnOutlined />}
                        /> */}
                        <BottomNavigationAction
                            className={"btn " + theme}
                            label="Boosts"
                            value="/boosts"
                            icon={<RocketLaunchOutlined />}
                        />
                        <BottomNavigationAction
                            className={"btn " + theme}
                            label="Tasks"
                            value="/tasks"
                            icon={<TodayOutlined />}
                        />
                    </BottomNavigation>
                </Paper>
            </Slide>
        </Box>
    );
};

export default Navbar;
