import { Box, Divider, Fade, Paper, Zoom } from "@mui/material";
import "./css/click-game.css";
import useSound from "use-sound";
import { grey } from "@mui/material/colors";
import { useRef, useState } from "react";
const ClickGame = ({
    energy,
    setEnergy,
    balance,
    coinsPerTap,
    karakterName,
    deadDuration,
    setKarakterHealth,
    karakterHealth,
    transition,
    warns,
    setWarns,
    setBalance,
    settings,
    backgroundImage,
    chunks,
    setChunks,
}) => {
    const [previousClickTime, setPreviousClickTime] = useState(0);
    const imageRef = useRef(null);
    const timeUntilNextEnemy = secondsToHms(deadDuration);
    const karakter_standing = `assets/characters/${karakterName}/standing.gif`;
    const karakter_hit = `assets/characters/${karakterName}/hit.gif`;
    const karakter_guard = `assets/characters/${karakterName}/guard.gif`;
    const hit_effects = [
        config.root + "/assets/characters/all/hit1.gif",
        config.root + "/assets/characters/all/hit2.gif",
        config.root + "/assets/characters/all/hit3.gif",
    ];
    const hitSounds = [
        config.root + "/assets/characters/all/hit1.mp3",
        config.root + "/assets/characters/all/hit2.mp3",
        config.root + "/assets/characters/all/hit3.mp3",
    ];
    const sounds = [
        useSound(hitSounds[0], { volume: 0.4 })[0],
        useSound(hitSounds[1], { volume: 0.4 })[0],
        useSound(hitSounds[2], { volume: 0.4 })[0],
    ];
    const hit_animations = [karakter_guard, karakter_hit];

    const mw =
        karakterName === "blaze"
            ? "100%"
            : karakterName === "raiden"
            ? "80%"
            : "90%";
    const h = karakterName === "raiden" ? "80%" : "70%";
    const config = require("../config.json");
    return (
        <Box
            sx={{
                display: "flex",
                backgroundImage: `url(${backgroundImage})`,
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center center",
                backgroundSize: "cover",
                justifyContent: "center",
                height: "100vh",
            }}
            container="true"
        >
            <img alt="" src={karakter_standing} hidden={true} />
            <img alt="" src={karakter_hit} hidden={true} />
            <img alt="" src={karakter_guard} hidden={true} />
            <img
                alt=""
                src={config.root + "/assets/characters/all/hit1.gif"}
                hidden={true}
            />
            <img
                alt=""
                src={config.root + "/assets/characters/all/hit2.gif"}
                hidden={true}
            />
            <img
                alt=""
                src={config.root + "/assets/characters/all/hit3.gif"}
                hidden={true}
            />
            {!deadDuration ? (
                <>
                    <Fade in={transition}>
                        <img
                            id="karakter"
                            ref={imageRef}
                            alt=""
                            src={karakter_standing}
                            style={{
                                imageRendering: "pixelated",
                                maxWidth: mw,
                                height: h,
                                position: "fixed",
                                bottom: 0,
                            }}
                            onTouchStart={karakter_onClick}
                        />
                    </Fade>
                    <img
                        id="effect"
                        alt=""
                        hidden
                        style={{
                            imageRendering: "pixelated",
                            position: "fixed",
                        }}
                    />
                </>
            ) : (
                <Zoom in={transition}>
                    <Box
                        sx={{ background: "none" }}
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                    >
                        <Paper
                            sx={{
                                background: "rgba(75, 75, 75, 0.225)",
                                borderRadius: "10px",
                                boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.75)",
                                backdropFilter: "blur(4.6px)",
                                textAlign: "center",
                                padding: "5px",
                                paddingBottom: "0px",
                                WebkitBackdropFilter: "blur(4.6px)",
                                maxWidth: "340px",
                                maxHeight: "120px",
                                fontSize: "24px",
                            }}
                        >
                            Looks like you can handle yourself... for now.
                            <Divider sx={{ border: 1, color: grey[900] }} />
                            lets see you in {timeUntilNextEnemy.hours}:
                            {timeUntilNextEnemy.minutes}:
                            {timeUntilNextEnemy.seconds}!
                        </Paper>
                    </Box>
                </Zoom>
            )}
        </Box>
    );

    function secondsToHms(seconds) {
        seconds = Number(seconds);
        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const secondsLeft = seconds % 60;

        const formattedHours = hours.toString().padStart(2, "0");
        const formattedMinutes = minutes.toString().padStart(2, "0");
        const formattedSeconds = secondsLeft.toString().padStart(2, "0");

        return {
            hours: formattedHours,
            minutes: formattedMinutes,
            seconds: formattedSeconds,
        };
    }

    async function karakter_onClick() {
        try {
            if (energy < coinsPerTap || deadDuration || karakterHealth <= 0)
                return;
            const currentTime = Date.now();
            const clickInterval = currentTime - previousClickTime;
            setPreviousClickTime(currentTime);
            if (clickInterval <= 30) {
                setWarns(warns + 1);
            } else {
                const updatedEnergy = energy - coinsPerTap;
                const updatedBalance = balance + coinsPerTap;
                const updatedHealth = karakterHealth - coinsPerTap;
                setEnergy(updatedEnergy);
                setBalance(updatedBalance);
                setChunks([
                    ...chunks,
                    { date: Date.now(), count: coinsPerTap },
                ]);
                setKarakterHealth(updatedHealth);
            }
            const karakterElement = document.getElementById("karakter");
            const effectElement = document.getElementById("effect");
            if (settings.karakterSounds) {
                sounds[Math.floor(Math.random() * sounds.length)]();
                if (navigator.vibrate) navigator.vibrate(50);
            }
            if (settings.karakterAnimations)
                karakterElement.src =
                    hit_animations[
                        Math.floor(Math.random() * hit_animations.length)
                    ];
            if (settings.karakterEffects) {
                effectElement.hidden = false;
                effectElement.src =
                    hit_effects[Math.floor(Math.random() * hit_effects.length)];
                effectElement.style.height =
                    Math.floor(Math.random() * 200) + 200 + "px";
                effectElement.style.bottom =
                    Math.floor(Math.random() * 50) + 10 + "vh";
                effectElement.style.left =
                    Math.floor(Math.random() * 30) + 15 + "vh";
            }

            if (settings.karakterAnimation || settings.karakterEffects) {
                setTimeout(() => {
                    if (settings.karakterAnimations)
                        karakterElement.src = karakter_standing;
                    if (settings.karakterEffects) {
                        effectElement.src = "";
                        effectElement.hidden = true;
                    }
                }, 400);
            }
        } catch (error) {
            console.log(error);
        }
    }
};

export default ClickGame;
