        description: `Quan Chi is a character shrouded in malevolence, and his presence reverberates throughout the Mortal Kombat series. His dark magic and necromancy skills set him apart. Hes not just a run-of-the-mill sorcerer; hes the most powerful one in the Netherrealm.`,
