const Register = ({ setRegistered, axios }) => {
    const [page, setPage] = useState(0);
    const [side, setSide] = useState(0);
    const [karakter, setKarakter] = useState("");
    const [loading, setLoading] = useState(false);
    const [modal, setModal] = useState(false);
    const telegram = window.Telegram.WebApp;
    const navigate = useNavigate();
    const Card = styled(Grid)(() => ({
        borderRadius: "8px",
        maxWidth: "450px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        paddingRight: 2,
    }));
    let result;

    switch (page) {
        // Select your side
        case 0:
            result = (
                <Grid
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        paddingLeft: "1rem",
                        paddingRight: "1rem",
                        height: "100vh",
                        width: "100%",
                        // paddingBottom: "14rem",
                        // paddingTop: "14.5rem",
                    }}
                    container="true"
                >
                    <Grid
                        xs={12}
                        display={"flex"}
                        justifyContent={"center"}
                        alignItems={"end"}
                        height={"50%"}
                    >
                        <span style={{ textAlign: "center" }}>
                            <h1>Choose your destiny!</h1>
                        </span>
                    </Grid>
                    <Grid
                        xs={12}
                        display={"flex"}
                        justifyContent={"center"}
                        alignItems={"start"}
                        height={"50%"}
                    >
                        <button
                            disabled={loading}
                            style={{
                                fontFamily: "mortal-kombat",
                                fontSize: "32px",
                                padding: "7px",
                                marginRight: "15px",
                                color: "white",
                            }}
                            onClick={() => {
                                setLoading(true);
                                telegram.BackButton.show();
                                telegram.BackButton.onClick(() => {
                                    setPage(0);
                                });
                                setTimeout(() => {
                                    setPage(1);
                                    setSide("good");
                                    setLoading(false);
                                }, 50);
                            }}
                            className={"register-btn blue"}
                        >
                            Light side
                        </button>
                        <button
                            disabled={loading}
                            style={{
                                fontFamily: "mortal-kombat",
                                fontSize: "32px",
                                padding: "7px",
                                color: "white",
                            }}
                            onClick={() => {
                                setLoading(true);
                                setTimeout(() => {
                                    setPage(2);
                                    telegram.BackButton.show();
                                    telegram.BackButton.onClick(() => {
                                        setPage(0);
                                    });
                                    setSide("bad");
                                    setLoading(false);
                                }, 50);
                            }}
                            className={"register-btn red"}
                        >
                            Dark side
                        </button>
                    </Grid>
                    {/* <Grid xs={6}></Grid> */}
                </Grid>
            );
            break;
        // Select your karakter (GOODs)
        case 1:
            result = (
                <Box
                    sx={{
                        background:
                            "linear-gradient(180deg, rgba(0,0,0,1) 40%, rgba(0,157,255,1) 100%);",
                        height: "100vh",
                        overflow: "hidden",
                    }}
                >
                    <Grid
                        container="true"
                        padding={1}
                        role="button"
                        gap={1.5}
                        display={"flex"}
                        justifyContent={"center"}
                    >
                        {goods.map((karakter, index) => (
                            <Card
                                zIndex={1}
                                key={index}
                                onClick={() => {
                                    setKarakter(karakter);
                                    setModal(true);
                                }}
                                xs={
                                    !!(goods.length % 2) &&
                                    index + 1 == goods.length
                                        ? 12
                                        : 5.7
                                }
                                maxWidth={"3rem"}
                                sx={{
                                    background: karakter.background,
                                    border: `1px solid ${karakter.color}`,
                                    boxShadow: `0 1px 3px ${karakter.color}`,
                                    padding: "5px",
                                }}
                            >
                                <img
                                    style={{ zIndex: 1 }}
                                    height={"80px"}
                                    src={karakter.image}
                                />
                                <Typography
                                    sx={{
                                        zIndex: 2,
                                        wordBreak: "break-word",
                                        fontSize: 21,
                                        typography: { xs: "h5", sm: "h4" },
                                        marginLeft: "3px",
                                    }}
                                >
                                    {karakter.name}
                                </Typography>
                            </Card>
                        ))}
                        <SwipeableDrawer
                            PaperProps={{
                                sx: {
                                    background: grey[900],
                                    borderRadius: "12px 12px 0 0",
                                    boxShadow: `0 -3px 18px ${karakter.color}`,
                                    borderTop: `1px solid ${karakter.color}`,
                                    textAlign: "center",
                                    padding: "5px",
                                    paddingBottom: "0px",
                                    padding: "1rem",
                                },
                            }}
                            slotProps={{
                                backdrop: {
                                    sx: { bgcolor: "rgba(15, 15, 15, .90)" },
                                },
                            }}
                            anchor="bottom"
                            sx={{ zIndex: 999999 }}
                            allowSwipeInChildren={true}
                            disableSwipeToOpen={true}
                            open={modal}
                            onOpen={() => {}}
                            onClose={() => {
                                setModal(false);
                            }}
                        >
                            <MaskedImage
                                mask={
                                    "linear-gradient(to bottom, black 50%, transparent 100%"
                                }
                            >
                                <img
                                    src={karakter.image}
                                    alt=""
                                    height={"300px"}
                                />
                            </MaskedImage>
                            <span
                                style={{
                                    textAlign: "start",
                                    marginTop: "-10px",
                                    letterSpacing: "1px",
                                }}
                            >
                                {karakter.description}
                            </span>
                            <div
                                style={{
                                    marginTop: "18px",
                                    marginBottom: "16px",
                                }}
                            >
                                <button
                                    onClick={() => {
                                        setKarakter(karakter);
                                        telegram.BackButton.hide();
                                        setPage(3);
                                        setModal(false);
                                    }}
                                    className={"btn " + karakter.btnColor}
                                    style={{
                                        fontFamily: "mortal-kombat",
                                        color: "white",
                                        padding: "16px",
                                        fontSize: "30px",
                                        letterSpacing: "5px",
                                    }}
                                >
                                    Choose
                                </button>
                            </div>
                        </SwipeableDrawer>
                    </Grid>
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            height: "17rem",
                        }}
                    >
                        <img
                            src={
                                config.root +
                                "/assets/images/mortal-kombat-logo.png"
                            }
                            alt=""
                            height={"200px"}
                        />
                    </Box>
                </Box>
            );
            break;
        // Select your karakter (BADs)
        case 2:
            result = (
                <Box
                    sx={{
                        background:
                            "linear-gradient(180deg, rgba(0,0,0,1) 40%, rgba(250,50,55,1) 100%);",
                        height: "100vh",
                        overflow: "hidden",
                    }}
                >
                    <Grid
                        container="true"
                        padding={1}
                        role="button"
                        gap={1.5}
                        display={"flex"}
                        justifyContent={"center"}
                    >
                        {bads.map((karakter, index) => (
                            <Card
                                zIndex={1}
                                key={index}
                                onClick={() => {
                                    setKarakter(karakter);
                                    setModal(true);
                                }}
                                xs={
                                    !!(bads.length % 2) &&
                                    index + 1 == bads.length
                                        ? 12
                                        : 5.7
                                }
                                maxWidth={"3rem"}
                                sx={{
                                    background: karakter.background,
                                    border: `1px solid ${karakter.color}`,
                                    boxShadow: `0 1px 3px ${karakter.color}`,
                                    padding: "5px",
                                }}
                            >
                                <img
                                    style={{ zIndex: 1 }}
                                    height={"80px"}
                                    src={karakter.image}
                                />
                                <Typography
                                    sx={{
                                        zIndex: 2,
                                        wordBreak: "break-word",
                                        fontSize: 21,
                                        typography: { xs: "h5", sm: "h4" },
                                        marginLeft: "3px",
                                    }}
                                >
                                    {karakter.name}
                                </Typography>
                            </Card>
                        ))}
                        <SwipeableDrawer
                            PaperProps={{
                                sx: {
                                    background: grey[900],
                                    borderRadius: "12px 12px 0 0",
                                    boxShadow: `0 -3px 18px ${karakter.color}`,
                                    borderTop: `1px solid ${karakter.color}`,
                                    textAlign: "center",
                                    padding: "5px",
                                    paddingBottom: "0px",
                                    padding: "1rem",
                                },
                            }}
                            slotProps={{
                                backdrop: {
                                    sx: { bgcolor: "rgba(15, 15, 15, .90)" },
                                },
                            }}
                            anchor="bottom"
                            sx={{ zIndex: 999999 }}
                            allowSwipeInChildren={true}
                            disableSwipeToOpen={true}
                            open={modal}
                            onOpen={() => {}}
                            onClose={() => {
                                setModal(false);
                            }}
                        >
                            <MaskedImage
                                mask={
                                    "linear-gradient(to bottom, black 50%, transparent 100%"
                                }
                            >
                                <img
                                    src={karakter.image}
                                    alt=""
                                    height={"300px"}
                                />
                            </MaskedImage>
                            <span
                                style={{
                                    textAlign: "start",
                                    marginTop: "-10px",
                                    letterSpacing: "1px",
                                }}
                            >
                                {karakter.description}
                            </span>
                            <div
                                style={{
                                    marginTop: "18px",
                                    marginBottom: "16px",
                                }}
                            >
                                <button
                                    onClick={() => {
                                        setKarakter(karakter);
                                        telegram.BackButton.hide();
                                        setPage(3);
                                        setModal(false);
                                    }}
                                    className={"btn " + karakter.btnColor}
                                    style={{
                                        fontFamily: "mortal-kombat",
                                        color: "white",
                                        padding: "16px",
                                        fontSize: "30px",
                                        letterSpacing: "5px",
                                    }}
                                >
                                    Choose
                                </button>
                            </div>
                        </SwipeableDrawer>
                    </Grid>
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            height: "17rem",
                        }}
                    >
                        <img
                            src={
                                config.root +
                                "/assets/images/mortal-kombat-logo.png"
                            }
                            alt=""
                            height={"200px"}
                        />
                    </Box>
                </Box>
            );
            break;
        // Fetch data
        case 3:
            const data = {
                karakter: { name: karakter.name, id: karakter.id },
            };
            if (telegram.initDataUnsafe.start_param)
                data.referredByUser = atob(telegram.initDataUnsafe.start_param);
            axios
                .post("/user/register", data)
                .then(() => {
                    navigate("/");
                })
                .catch(() => {
                    telegram.close();
                });
            break;
    }
    return result;

    function MaskedImage({ mask, children }) {
        return (
            <div
                style={{
                    WebkitMaskImage: mask,
                    maskImage: mask,
                }}
            >
                {children}
            </div>
        );
    }
};

export default Register;
