import React, { useState, useEffect, StrictMode } from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import "./components/css/index.css";
import { BrowserRouter } from "react-router-dom";
import {
    createTheme,
    CssBaseline,
    LinearProgress,
    ThemeProvider,
} from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2/Grid2";
import { grey } from "@mui/material/colors";
import { Instagram, Telegram, YouTube } from "@mui/icons-material";
import Axios from "axios";
import { usePersistState } from "@printy/react-persist-state/dist";
import { QRCodeSVG } from "qrcode.react";
const config = require("./config.json");
const MortalKombatFont = config.root + "/assets/fonts/Mortal-Kombat-MK11.otf";
const telegram = window.Telegram.WebApp;
let axios = Axios.create({
    baseURL: config.host + "/api",
    headers: {
        Accept: "application/json",
        Authorization: "Bearer none",
    },
    timeout: 10_000,
});

const app_theme = createTheme({
    palette: {
        background: {
            default: "#000",
            paper: "#000",
        },
        primary: {
            main: "#fff",
        },
        secondary: { main: "#fff" },
        text: {
            primary: "#fff",
            secondary: "#fff",
        },
    },

    typography: {
        fontFamily: "mortal-kombat",
    },
    components: {
        MuiCssBaseline: {
            styleOverrides: `@font-face {
                            font-family: mortal-kombat;
                            src: url(${MortalKombatFont});
                            }
                            scrollbarWidth: none;
                            &::-webkit-scrollbar: {
                                display: none;
                            }
                            &-ms-overflow-style:: {
                                display: none;
                            }`,
        },
    },
});

telegram.isClosingConfirmationEnabled = true;
telegram.SettingsButton.isVisible = true;

let loadingScreen = {
    bg: "",
    telegram: "",
    instagram: "",
    youtube: "",
};
axios
    .get("/status/loading")
    .then(async ({ data }) => {
        loadingScreen = data;
        const root = ReactDOM.createRoot(document.getElementById("root"));
        root.render(<Render />);
        telegram.ready();
    })
    .catch((error) => {
        console.log(error);
        telegram.close();
    });

function Render() {
    const [loading, setLoading] = useState(true);
    const [token, setToken] = usePersistState("");
    const [prices, setPrices] = usePersistState({});
    const [user, setUser] = usePersistState({});
    const [karakter, setKarakter] = usePersistState({});
    const [themeColor, setTheme] = usePersistState("");

    useEffect(() => {
        setTimeout(async () => {
            try {
                await setData(window.Telegram.WebApp.initData);
                setLoading(false);
            } catch (error) {
                try {
                    if (error.response?.data?.token) {
                        console.log(error.response.data);
                        const token = `Bearer ${error.response.data.token}`;
                        setToken(token);
                        axios.defaults.headers.common["Authorization"] = token;
                        await setData(window.Telegram.WebApp.initData);
                        return setLoading(false);
                    } else {
                        console.log(error);
                        telegram.close();
                    }
                } catch (error) {
                    console.log(error);
                    return telegram.close();
                }
            }
        }, 500);
    }, []);

    if (loading)
        return (
            <StrictMode>
                <ThemeProvider theme={app_theme}>
                    <CssBaseline />
                    <Grid
                        sx={{
                            overflow: "hidden",
                            height: "100vh",
                            width: "auto",
                            backgroundImage: `url(${loadingScreen.bg})`,
                            backgroundRepeat: "no-repeat",
                            backgroundPosition: "center center",
                            backgroundSize: "cover",
                            zIndex: 1000000,
                            textAlign: "center",
                            fontFamily: "mortal-kombat",
                            display: "flex",
                            justifyContent: "center",
                            flexDirection: "row",
                        }}
                        container="true"
                    >
                        <Grid xs={12} paddingTop={3}>
                            <div style={{ fontSize: "50px" }}>Karakter</div>
                            <div
                                style={{
                                    fontSize: "35px",
                                    marginTop: "-1.4rem",
                                    letterSpacing: "7px",
                                }}
                            >
                                Kombat
                            </div>
                        </Grid>
                        <Grid
                            xs={12}
                            padding={2.5}
                            display={"flex"}
                            alignItems={"end"}
                            justifyContent={"center"}
                            direction={"row"}
                        >
                            <Grid
                                container="true"
                                direction={"column"}
                                alignItems={"center"}
                            >
                                <Grid
                                    container="true"
                                    display={"flex"}
                                    columnGap={0.3}
                                    marginBottom={".35rem"}
                                >
                                    <Grid xs={4}>
                                        <a href={loadingScreen.telegram || "#"}>
                                            <button
                                                style={{
                                                    textAlign: "center",
                                                    backgroundColor:
                                                        grey[
                                                            loadingScreen.telegram
                                                                ? 200
                                                                : 600
                                                        ],
                                                    border: "1px solid black",
                                                    padding: 4,
                                                    borderRadius: "10px",
                                                    color: grey[900],
                                                }}
                                            >
                                                <Telegram />
                                            </button>
                                        </a>
                                    </Grid>
                                    <Grid xs={4}>
                                        <a
                                            href={
                                                loadingScreen.instagram || "#"
                                            }
                                        >
                                            <button
                                                style={{
                                                    textAlign: "center",
                                                    backgroundColor:
                                                        grey[
                                                            loadingScreen.instagram
                                                                ? 200
                                                                : 600
                                                        ],
                                                    border: "1px solid black",
                                                    padding: 4,
                                                    borderRadius: "10px",
                                                    color: grey[900],
                                                }}
                                            >
                                                <Instagram />
                                            </button>
                                        </a>
                                    </Grid>
                                    <Grid xs={4}>
                                        <a href={loadingScreen.youtube || "#"}>
                                            <button
                                                style={{
                                                    textAlign: "center",
                                                    backgroundColor:
                                                        grey[
                                                            loadingScreen.youtube
                                                                ? 200
                                                                : 600
                                                        ],
                                                    border: "1px solid black",
                                                    padding: 4,
                                                    borderRadius: "10px",
                                                    color: grey[900],
                                                }}
                                            >
                                                <YouTube />
                                            </button>
                                        </a>
                                    </Grid>
                                </Grid>
                                <Grid xs={12} width={"100%"}>
                                    <LinearProgress
                                        color="success"
                                        sx={{
                                            minWidth: "16.5rem",
                                            maxWidth: "23.5rem",
                                        }}
                                    />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </ThemeProvider>
            </StrictMode>
        );

    // if (telegram.platform === "unknown" || telegram.platform === "tdesktop")
    // return (
    //     <ThemeProvider theme={app_theme}>
    //         <CssBaseline />
    //         <Grid
    //             sx={{
    //                 color: "white",
    //                 fontFamily: "mortal-kombat",
    //                 letterSpacing: "1px",
    //                 display: "flex",
    //                 alignItems: "center",
    //                 justifyContent: "center",
    //                 height: "100vh",
    //                 width: "100%",
    //                 padding: "1rem",
    //             }}
    //             container="true"
    //         >
    //             <Grid xs={12} textAlign={"center"}>
    //                 <h1>Mobile gaming rocks</h1>
    //                 <h4>
    //                     Please scan this qrcode and join us with your mobile
    //                     
    //                 </h4>
    //             </Grid>
    //             <Grid
    //                 sx={{
    //                     padding: "6px",
    //                     display: "flex",
    //                     alignItems: "center",
    //                     justifyContent: "center",
    //                 }}
    //                 xs={12}
    //             >
    //                 <QRCodeSVG
    //                     size={250}
    //                     value="https://t.me/KarakterKombatBot"
    //                     level="Q"
    //                     imageSettings={{
    //                         src: config.root + "/assets/images/logo1.png",
    //                         height: 65,
    //                         width: 65,
    //                         excavate: true,
    //                         // x: 0,
    //                         // y: 0
    //                     }}
    //                     imageRendering={{}}
    //                 />
    //             </Grid>
    //         </Grid>
    //     </ThemeProvider>
    // );

    return (
        <React.StrictMode>
            <meta
                name="viewport"
                content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"
            />
            <BrowserRouter>
                <ThemeProvider theme={app_theme}>
                    <CssBaseline />
                    <App
                        token={token}
                        karakter={karakter}
                        prices={prices}
                        user={user}
                        setKarakter={setKarakter}
                        setTheme={setTheme}
                        setUser={setUser}
                        setData={() => setData(window.Telegram.WebApp.initData)}
                        theme={themeColor}
                    />
                </ThemeProvider>
            </BrowserRouter>
        </React.StrictMode>
    );

    async function setData(__data) {
        const data = btoa(__data);
        // const data = btoa(
        //     "query_id=AAHad9NZAgAAANp301mN9a14&user=%7B%22id%22%3A5801998298%2C%22first_name%22%3A%22Amir%F0%9F%A6%B4%22%2C%22last_name%22%3A%22%22%2C%22username%22%3A%22ElAmir_s%22%2C%22language_code%22%3A%22en%22%2C%22allows_write_to_pm%22%3Atrue%7D&auth_date=1722708323&hash=b98cc16e35e90514c1850058c4df3d6a082af3943f1c8a5396332b083c7e0f04"
        // );
        let _data = await axios.get("/status/status?data=" + data);
        if (_data.data.token) {
            console.log("Setting token");
            const _token = `Bearer ${_data.data.token}`;
            axios = Axios.create({
                baseURL: config.host + "/api",
                headers: {
                    Accept: "application/json",
                    Authorization: _token,
                },
                timeout: 10_000,
            });
            setToken(_token);
        }
        _data = await axios.get("/status/status?data=" + data);
        const _prices = await axios.get("/upgrade/prices");
        _data.data.user.settings = JSON.parse(
            localStorage.getItem("settingsKey")
        ) || {
            karakterAnimations: true,
            karakterSounds: true,
            karakterEffects: true,
            menuSounds: true,
        };
        setUser(_data.data.user);
        setKarakter(_data.data.karakter);
        setTheme(_data.data.karakter.theme.color);
        setPrices(_prices.data);
    }
}
