import { CopyAllOutlined, Telegram } from "@mui/icons-material";
import { Fade, Paper, Slide } from "@mui/material";
import { grey } from "@mui/material/colors";
import Grid from "@mui/material/Unstable_Grid2/Grid2";
import Particles from "@tsparticles/react";
import React, { useMemo } from "react";
import { TypeAnimation } from "react-type-animation";

const Friends = ({ transition, friends, theme, uuid, P }) => {
    const telegram = window.Telegram.WebApp;
    const inviteLink = `https://t.me/KarakterKombatBot/start?startapp=${btoa(
        uuid
    )}`;const config = require("../config.json");
    const Particle = useMemo(() => <Particles options={P} />, []);

    return (
        <div>
            {Particle}
            <Grid
                sx={{
                    height: "100vh",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    background: "none",
                    backdropFilter: "blur(0px)",
                    WebkitBackdropFilter: "blur(0px)",
                    border: "1px solid " + theme,
                    boxShadow: "inset 0 5px 20px " + theme,
                    zIndex: 1,
                }}
                container="true"
                overflow={"hidden"}
            >
                <Grid
                    xs={12}
                    width="100%"
                    height="14vh"
                    position={"fixed"}
                    top={0}
                >
                    <Slide in={transition}>
                        <Paper
                            elevation={12}
                            sx={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                background: grey[900],
                                borderRadius: "0",
                                width: "100%",
                                borderBottom: "1px solid " + theme,
                            }}
                        >
                            <h1
                                style={{
                                    width: "100%",
                                    fontSize: 30,
                                    textAlign: "center",
                                    color: "red",
                                }}
                            >
                                <span style={{ color: theme }}>
                                    <TypeAnimation
                                        sequence={[
                                            "Your allies",
                                            2000,
                                            friends.length === 1
                                                ? `${friends.length} Allie`
                                                : friends.length > 1
                                                ? `${friends.length} Allies`
                                                : "You are so weak...",
                                            2000,
                                        ]}
                                        repeat={Infinity}
                                        cursor={true}
                                        speed={10}
                                    />
                                </span>
                            </h1>
                        </Paper>
                    </Slide>
                </Grid>
                <Grid
                    xs={12}
                    padding={2}
                    paddingTop={3.5}
                    paddingBottom={3.5}
                    maxWidth={"744px"}
                    marginTop={"1rem"}
                    height={"75vh"}
                    marginBottom={"2rem"}
                >
                    <Fade in={transition}>
                        <Grid
                            sx={{
                                background: "rgba(50, 50, 50, 0.20)",
                                borderRadius: "10px",
                                boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.8)",
                                backdropFilter: "blur(4px)",
                                WebkitBackdropFilter: "blur(4px)",
                                width: "100%",
                                height: "100%",
                                padding: 1.5,
                                border: "1px solid " + theme,
                            }}
                            container="true"
                            direction={"row"}
                            rowGap={1.2}
                            overflow={"auto"}
                        >
                            {friends.length <= 50 && friends.length !== 0 ? (
                                friends.list.map((friend, index) => (
                                    <Grid xs={12}>
                                        <Slide
                                            in={transition}
                                            direction={
                                                index % 2 === 0
                                                    ? "left"
                                                    : "right"
                                            }
                                        >
                                            <Paper
                                                sx={{
                                                    padding: "0.3rem 0.5rem 0.3rem 0.5rem",
                                                }}
                                                className={`btn ${theme}`}
                                            >
                                                <Grid container="true">
                                                    <Grid xs={6}>
                                                        <span
                                                            style={{
                                                                fontSize:
                                                                    "20px",
                                                            }}
                                                        >
                                                            {friend.name}
                                                        </span>
                                                    </Grid>
                                                    <Grid
                                                        xs={6}
                                                        textAlign={"end"}
                                                    >
                                                        <span
                                                            style={{
                                                                fontSize:
                                                                    "18px",
                                                            }}
                                                        >
                                                            {friend.friends}
                                                        </span>{" "}
                                                        <span
                                                            style={{
                                                                fontSize:
                                                                    "12px",
                                                            }}
                                                        >
                                                            Friends
                                                        </span>{" "}
                                                    </Grid>
                                                    <Grid
                                                        xs={6}
                                                        display={"flex"}
                                                        columnSpacing={2}
                                                        justifyContent={"start"}
                                                        alignItems={"center"}
                                                    >
                                                        <Grid container="true">
                                                            <Grid>
                                                                <img
                                                                    src={
                                                                        config.root + "/assets/images/logo1.png"
                                                                    }
                                                                    alt=""
                                                                    height={
                                                                        "25px"
                                                                    }
                                                                    width={
                                                                        "25px"
                                                                    }
                                                                />
                                                            </Grid>
                                                            <Grid
                                                                style={{
                                                                    fontSize:
                                                                        "18px",
                                                                }}
                                                            >
                                                                <span
                                                                    style={{
                                                                        fontSize:
                                                                            "18px",
                                                                    }}
                                                                >
                                                                    +{friend.profit.toLocaleString()}
                                                                </span>{" "}
                                                                <span
                                                                    style={{
                                                                        fontSize:
                                                                            "12px",
                                                                    }}
                                                                >
                                                                    Koins
                                                                </span>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                            </Paper>
                                        </Slide>
                                    </Grid>
                                ))
                            ) : friends.length === 0 ? (
                                <h1 style={{ color: grey[100] }}>
                                    "You dont have any allies. Why you are so
                                    weak?"
                                </h1>
                            ) : (
                                <h1 style={{ color: grey[100] }}>
                                    "You have more than{" "}
                                    <span style={{ color: "orange" }}>50</span>{" "}
                                    allies and we can't show all of them"
                                </h1>
                            )}
                        </Grid>
                    </Fade>
                    <Grid
                        marginTop={1.7}
                        display={"flex"}
                        justifyContent={"center"}
                        alignItems={"center"}
                        columnGap={1}
                    >
                        <Slide in={transition} direction="right">
                            <Paper
                                className={`btn ${theme}`}
                                sx={{
                                    fontSize: {
                                        xs: "12px",
                                        sm: "12px",
                                    },
                                    padding: {
                                        xs: "4px",
                                        sm: "10px",
                                    },
                                    height: "43px",
                                    paddingLeft: 8,
                                }}
                            >
                                <span>{inviteLink}</span>
                            </Paper>
                        </Slide>
                        <Fade in={transition}>
                            <button
                                onClick={() => {
                                    navigator.clipboard.writeText(inviteLink);
                                }}
                                className={`btn ${theme}`}
                                style={{ color: grey[100], padding: 6.5 }}
                            >
                                <CopyAllOutlined />
                            </button>
                        </Fade>
                        <Slide in={transition} direction="left">
                            <button
                                onClick={() => {
                                    telegram.openTelegramLink(
                                        `https://t.me/share/url?url=${inviteLink}&text=${encodeURI(
                                            `\nKarakterKombat: Mortal Clicker  Test Your Might!



Calling all warriors! KarakterKombat awaits. Click your way to victory, level up, and unlock fatalities. Join me in the arena!

Finish Him! 

Short and sweetjust like a flawless victory! 

By the way, which Mortal Kombat character inspires your inner kombatant? Scorpions Get over here! or Sub-Zeros icy coolness? `
                                        )}`
                                    );
                                }}
                                className={`btn ${theme}`}
                                style={{ color: grey[100], padding: 6.5 }}
                            >
                                <Telegram />
                            </button>
                        </Slide>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    );
};

export default Friends;
